@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article, aside, footer, header, nav, section {
  display: block; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

figcaption, figure, main {
  display: block; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace,monospace;
  font-size: 1em; }

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted; }

b, strong {
  font-weight: inherit; }

b, strong {
  font-weight: bolder; }

code, kbd, samp {
  font-family: monospace,monospace;
  font-size: 1em; }

dfn {
  font-style: italic; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

audio, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button, input {
  overflow: visible; }

button, select {
  text-transform: none; }

button, html [type=button], [type=reset], [type=submit] {
  -webkit-appearance: button; }

button::-moz-focus-inner, [type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring, [type=button]:-moz-focusring, [type=reset]:-moz-focusring, [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: .35em .75em .625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  display: inline-block;
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type=checkbox], [type=radio] {
  box-sizing: border-box;
  padding: 0; }

[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
  height: auto; }

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details, menu {
  display: block; }

summary {
  display: list-item; }

canvas {
  display: inline-block; }

template {
  display: none; }

[hidden] {
  display: none; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

form p, form label {
  font-size: 20px; }

input[type=number] {
  padding-left: 10px;
  background-color: yellow;
  text-align: center; }

input[type=radio] {
  margin: 10px; }

button {
  background-color: pink;
  margin-top: 20px; }

main {
  height: 90%; }

.wrapper {
  text-align: center; }

body {
  background-color: lightcyan;
  height: 100vh; }

.tall {
  height: 100%;
  display: sticky;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 110, 255, 0.5)), color-stop(#006eff), to(rgba(0, 110, 255, 0.5)));
  background-image: linear-gradient(to right, rgba(0, 110, 255, 0.5), #006eff, rgba(0, 110, 255, 0.5)); }

.scroll {
  height: 50vh;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative; }

.form h3 {
  float: left;
  display: block;
  width: calc(100%/3); }

.lists {
  width: calc((100%/3) - 20px);
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  float: left;
  overflow: visible;
  height: 100%; }

.by {
  size: 50px; }

.tv a, .movies a {
  padding-bottom: 10px;
  width: 100%;
  float: left; }

.tv p, .movies p {
  padding-bottom: 20px; }

img {
  max-height: 150px; }

.dadjokes {
  width: 100%;
  clear: both;
  height: 10%; }

.loading {
  width: 100%;
  float: left; }

.center {
  padding-left: 45%; }

svg {
  text-align: center; }

header {
  font-size: 40px;
  display: block;
  padding: 10px;
  font-family: 'Bangers', cursive; }

